import { HttpClient } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { BehaviorSubject } from 'rxjs'
import { map } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { User, UserBody } from '../models/user.models'

@Injectable({
  providedIn: 'root',
})
export class UserService {
  private baseUrl = environment.apiUrl

  public users: BehaviorSubject<User[]> = new BehaviorSubject(null)

  constructor(private http: HttpClient) {}

  fetchUsers() {
    return new Promise((resolve, reject) => {
      this.http.get<User[]>(`${this.baseUrl}/users/admin`).subscribe(
        (data) => {
          this.users.next(data)
          resolve(data)
        },
        (err) => {
          reject(err)
        }
      )
    })
  }

  getUserAuth() {
    return this.http.get<User>(`${this.baseUrl}/users/me`)
  }

  saveUser(body: UserBody) {
    return this.http.post<User>(`${this.baseUrl}/users/admin`, body).pipe(
      map((res) => {
        this.fetchUsers()
        return res
      })
    )
  }

  updateUser(body: UserBody, id: string, isAdmin: boolean = true) {
    const url = isAdmin ? `${this.baseUrl}/users/admin/${id}` : `${this.baseUrl}/users/me`
    return this.http.put<User>(url, body).pipe(
      map((res) => {
        this.fetchUsers()
        return res
      })
    )
  }

  deleteUser(id: string) {
    return this.http.delete<User>(`${this.baseUrl}/users/admin/${id}`).pipe(
      map((res) => {
        this.fetchUsers()
        return res
      })
    )
  }

  changePassword(body: { current_password: string; new_password: string }) {
    return this.http.post(`${this.baseUrl}/users/me/changePassword`, body)
  }
}
