import { Component, AfterViewInit, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { DialogService } from "src/app/shared/dialogs/dialog.service";

@Component({
  selector: "app-results",
  templateUrl: "./results.component.html",
  styleUrls: ["./results.component.scss"],
})
export class ResultsComponent implements AfterViewInit {
  displayedColumns = ["driver", "invoice", "date", "bill_to", "ship_from", "ship_to", "amount", "actions"];
  pageSizeOptions: number[] = [12, 24, 50, 100];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  items: MatTableDataSource<any>;
  constructor(private dialogService: DialogService, public dialog: MatDialog) {
    this.items = new MatTableDataSource(mock_invoices);
  }

  openEditDialog(invoice: any) {
    this.dialog.closeAll();
    // this.dialog.open<EditUserDialogComponent, EditUserDialogData>(EditUserDialogComponent, {
    //   width: "700px",
    //   data: {
    //     invoice,
    //   },
    // });
  }

  onDelete() {
    this.dialogService.showConfirm({ title: "Delete invoice", msg: "Are you sure?" }, () => console.log("DELETE INVOICE"));
  }

  applyFilter(filterValue: string) {
    this.items.filter = filterValue.trim().toLocaleLowerCase();
  }

  ngAfterViewInit() {
    this.items.sort = this.sort;
    this.items.paginator = this.paginator;
  }
}

