import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Subscription } from "rxjs";
import { Invoice, InvoiceStatus } from "../business/models/invoice.models";
import { User } from "../business/models/user.models";
import { CompanyService } from "../business/services/company.service";
import { UserService } from "../business/services/user.service";
import { NotificationService } from "../shared/notifications/notification.service";

interface Statuses {
  value: InvoiceStatus;
  label: string;
}

const statuses: Statuses[] = [
  { value: "DRAFT", label: "Draft" },
  { value: "SUBMITTED_FOR_REVIEW", label: "Submitted for review" },
  { value: "SUBMITTED_TO_CUSTOMER", label: "Submitted to customers" },
  { value: "PAID", label: "Paid" },
];

@Component({
  selector: "app-search",
  templateUrl: "./search.component.html",
  styleUrls: ["./search.component.scss"],
})
export class SearchComponent implements OnInit, AfterViewInit, OnDestroy {
  subscriptions: Subscription[] = [];
  users: User[];
  statuses = statuses;
  searchForm: FormGroup;
  loading = false;
  showResults = false;
  foundInvoices: Invoice[] = [];
  items: MatTableDataSource<Invoice>;
  displayedColumns = ["driver", "invoice", "date", "bill_to", "ship_from", "ship_to", "amount"];
  pageSizeOptions: number[] = [12, 24, 50, 100];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  constructor(
    private userService: UserService,
    private fb: FormBuilder,
    private companyService: CompanyService,
    private notificationService: NotificationService
  ) {
    this.items = new MatTableDataSource([]);
    const sub = this.userService.users.subscribe((data) => {
      this.users = data;
    });
    this.subscriptions.push(sub);
  }

  ngOnInit(): void {
    this.userService.fetchUsers();

    this.searchForm = this.fb.group({
      date_from: [null, Validators.required],
      date_to: [null, Validators.required],
      invoice_number: [null],
      invoice_status: [""],
      ship_from_name: [null],
      ship_to_name: [null],
      vin: [null],
      driver_id: [""],
      comments: [null],
    });

    //custom table filters
    this.items.filterPredicate = (data: Invoice, filter: string) => {
      return (
        data.driver.first_name.toLowerCase().indexOf(filter) !== -1 ||
        data.driver.last_name.toLowerCase().indexOf(filter) !== -1 ||
        data.number?.toString().toLowerCase().indexOf(filter) !== -1 ||
        data.date?.toString().toLowerCase().indexOf(filter) !== -1 ||
        data.bill_to?.name.toLowerCase().indexOf(filter) !== -1 ||
        (data.ship_from || data.quick_ship_from)?.name.toLowerCase().indexOf(filter) !== -1 ||
        (data.ship_to || data.quick_ship_to)?.name.toLowerCase().indexOf(filter) !== -1 ||
        data.total.toString().toLowerCase().indexOf(filter) !== -1
      );
    };
  }

  onSubmit() {
    if (this.searchForm.invalid) {
      return Object.values(this.searchForm.controls).forEach((control) => {
        if (control instanceof FormGroup) {
          Object.values(control.controls).forEach((control) => control.markAsTouched());
        } else {
          control.markAsTouched();
        }
      });
    } else {
      this.loading = true;

      const { date_from, date_to, ...rest } = this.searchForm.value;
      const body = rest;
      body.date_from = `${date_from.year}-${String(date_from.month)?.padStart(2, "0")}-${String(date_from.day)?.padStart(2, "0")}`;
      body.date_to = `${date_to.year}-${String(date_to.month)?.padStart(2, "0")}-${String(date_to.day)?.padStart(2, "0")}`;

      this.companyService.search(body).subscribe(
        (res) => {
          this.items.data = res;
          this.loading = false;
          this.showResults = true;
        },
        (err) => {
          this.notificationService.error("Unexpected error");
          this.loading = false;
          this.items.data = [];
          this.showResults = false;
        }
      );
    }
  }

  resetSearch(){
    this.items.data = [];
    this.searchForm.reset()
    this.showResults = false;
  }

  applyFilter(filterValue: string) {
    this.items.filter = filterValue.trim().toLocaleLowerCase();
  }

  ngAfterViewInit() {
    this.items.sort = this.sort;
    this.items.paginator = this.paginator;
  }

  ngOnDestroy() {
    this.subscriptions.forEach((sub) => sub.unsubscribe());
  }
}
