import { Injectable } from '@angular/core'
import { MatDialog, MatDialogRef } from '@angular/material/dialog'
import { ConfirmDialogComponent, ConfirmDialogData } from './confirm/confirm-dialog.component'
import { LoaderDialogComponent, LoaderDialogComponentData } from './loader/loader-dialog.component'

@Injectable({
  providedIn: 'root',
})
export class DialogService {
  currentLoaderDialogRef: MatDialogRef<LoaderDialogComponent>

  constructor(public dialog: MatDialog) {}

  showConfirm(args: ConfirmDialogData, onConfirm: (...args: any) => any) {
    this.dialog
      .open<ConfirmDialogComponent, ConfirmDialogData>(ConfirmDialogComponent, {
        width: '400px',
        data: args,
      })
      .afterClosed()
      .subscribe((confirm: boolean) => {
        if (confirm) {
          onConfirm()
        }
      })
  }

  showLoader() {
    this.dialog
      .open<LoaderDialogComponent, LoaderDialogComponentData>(LoaderDialogComponent, {
        width: '300px',
        data: {
          setDialogRef: (dialogRef) => {
            this.currentLoaderDialogRef = dialogRef
          },
        },
        disableClose: true,
      })
      .afterClosed()
      .subscribe()
  }

  hideLoader() {
    this.currentLoaderDialogRef?.close()
    this.currentLoaderDialogRef = null
  }
}
